<template>
  <div>
    <ui-h-1>Contact me</ui-h-1>
    <div class="my-4 text-light-primary dark:text-dark-primary">
      <p>Feel free to get in touch.</p>
      <div class="text-center my=2">
        <a href="mailto:poncey314@icloud.com"> <font-awesome-icon icon="fa-solid fa-envelope" /> Email: poncey314@icloud.com </a>
      </div>
      <div class="text-center">
        <font-awesome-icon icon="fa-solid fa-map-marker" /> No. 2001 Longxiang Avenue, Shenzhen City, Guangdong Province, China
      </div>
      <div class="flex items-center justify-center my-4">
        <div id="container"></div>
      </div>
    </div>

    <!--Collaborators and Friends-->
    <ui-h-1>Collaborators and Friends</ui-h-1>
    <div>
      <ul role="list" class="divide-y divide-gray-300 dark:divide-gray-700">
        <li v-for="person in people" :key="person.email" class="flex justify-between gap-x-6 py-5">
          <a :href="person.website" target="_blank">
            <div class="flex min-w-0 gap-x-4 text hoverdiv">
              <img class="h-12 w-12 flex-none object-cover rounded-full bg-gray-50" :src="person.imageUrl" alt="" />
              <div class="min-w-0 flex-auto">
                <p class="text-sm font-semibold leading-6 text-light-primary dark:text-dark-primary">{{ person.name }}</p>
                <p class="mt-1 truncate text-xs leading-5 text-light-primary dark:text-dark-primary">{{ person.affiliate }}</p>
              </div>
            </div>
          </a>
        </li>
      </ul>
    </div>
    <!--Visitors Map-->
    <ui-h-1>Visitors Map</ui-h-1>
    <div class="flex items-center justify-center my-4">
      <div ref="clustrMaps" style="width: 32%; height: 240px"></div>
    </div>
  </div>
</template>

<script>
import { themes } from "@/store";
import UiH1 from "@/components/ui/UiH1.vue";
import AMapLoader from "@amap/amap-jsapi-loader";

const people = [
  {
    name: "Tzong-Yi LEE",
    website: "https://sites.google.com/view/biomics/biomics-lab",
    affiliate: "Professor, National Yang Ming Chiao Tung University (NYCU).",
    imageUrl: "https://www.cs.nycu.edu.tw/storage/avatars/rT8vBuNcCcPt1gijH6sJNxXP7s3wtmakGcWRPxbb.png",
  },
  {
    name: "Yunheng WU",
    website: "https://yunhengwu-ib.github.io/",
    affiliate: "Ph.D. Candidate, Nagoya University.",
    imageUrl: "https://yunhengwu-ib.github.io/yunheng_wu.jpg",
  },
];

export default {
  components: { UiH1 },
  name: "Contacts",
  computed: {
    currentThemeIsDark: {
      get() {
        return this.$store.state.theme === themes.dark;
      },
      set(newThemeIsDark) {
        this.changeMapTheme(newThemeIsDark ? "dark" : "white");
      },
    },
  },
  data() {
    return {
      people: people,
      longitude: 123.45, // 替换成你的经度
      latitude: 67.89, // 替换成你的纬度
      mapThemes: {
        white: "amap://styles/macaron", // 白天模式主题URL
        dark: "amap://styles/dark", // 黑夜模式主题URL
      },
    };
  },
  methods: {
    initAMap() {
      AMapLoader.load({
        key: "09e744805d024200c36263b754c5fb8f", // 申请好的Web端开发者Key，首次调用 load 时必填
        version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      })
        .then((AMap) => {
          this.$watch("currentThemeIsDark", (newThemeIsDark) => {
            this.currentThemeIsDark = newThemeIsDark;
          });
          this.map = new AMap.Map("container", {
            // 设置地图容器id
            viewMode: "3D", // 是否为3D地图模式
            zoom: 14, // 初始化地图级别
            center: [114.213107, 22.689497], // 初始化地图中心点位置
            lang: "zh-en",
            mapStyle: this.currentThemeIsDark ? "amap://styles/dark" : "amap://styles/macaron",
          });
          const marker = new AMap.Marker({
            position: new AMap.LngLat(114.213107, 22.689497), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
            title: "CUHK-Shenzhen",
          });
          this.map.add(marker);
          this.map.setLang("en");
        })
        .catch((e) => {
          console.log(e);
        });
    },
    changeMapTheme(theme) {
      this.map.setMapStyle(this.mapThemes[theme]);
    },
    initClustrMap() {
      this.script = document.createElement("script");
      this.script.setAttribute("type", "text/javascript");
      this.script.setAttribute("id", "clstr_globe");
      this.script.setAttribute("src", "//clustrmaps.com/globe.js?d=z0VBFTI7dovV6E1EW4VZM_3hPtLOe6md1a6DpdgEGiI");
      let targetDiv = this.$refs.clustrMaps;
      targetDiv.appendChild(this.script);
    },
  },
  mounted() {
    this.initAMap();
    this.initClustrMap();
  },
  unmounted() {
    this.map?.destroy();
    this.script?.destroy();
  },
};
</script>

<style scoped>
#container {
  width: 80%;
  height: 200px;
}
</style>
